package com.cordylussoftware.nocta.components

import androidx.compose.runtime.Composable
import com.cordylussoftware.nocta.components.elements.HeaderText
import com.cordylussoftware.nocta.components.elements.InCardText
import com.cordylussoftware.nocta.components.elements.LinkText
import com.cordylussoftware.nocta.utils.Res
import com.varabyte.kobweb.compose.css.ObjectFit
import com.varabyte.kobweb.compose.foundation.layout.*
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import org.jetbrains.compose.web.css.px

@Composable
fun VerticalColumn(breakpoint: Breakpoint, modifier: Modifier = Modifier) {
    Column(
        modifier = modifier
            .background(Res.Theme.LIGHT_BLUE.color),
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = Alignment.Start,
    ) {
        Image(
            modifier = Modifier.fillMaxSize()
                .objectFit(ObjectFit.Cover),
            src = Res.Image.NOCTA_SCREENSHOTS
        )
        Box(modifier = Modifier.height(30.px))
        HeaderText(text = Res.String.PAGE_TITLE)
        Box(modifier = Modifier.height(16.px))
        InCardText(text = Res.String.APP_DESCRIPTION)
        Box(modifier = Modifier.height(8.px))
        Row(
            modifier = Modifier.fillMaxWidth(),
            horizontalArrangement = Arrangement.Start
        ) {
            LinkText(text = Res.String.LINK_NAME_GOOGLE_PLAY)
            InCardText(text = Res.String.USEFUL_LINKS_LINK_DIVIDER, modifier = Modifier.padding(leftRight = 12.px))
            LinkText(text = Res.String.LINK_NAME_4PDA)
            InCardText(text = Res.String.USEFUL_LINKS_LINK_DIVIDER, modifier = Modifier.padding(leftRight = 12.px))
            LinkText(text = Res.String.LINK_NAME_TELEGRAM)
        }
        Box(modifier = Modifier.height(64.px))
        HeaderText(modifier = Modifier, text = Res.String.USEFUL_LINKS_TITLE)
        Box(modifier = Modifier.height(32.px))
        InCardText(modifier = Modifier, text = Res.String.USEFUL_LINKS_SUBTITLE1)
        LinkText(modifier = Modifier.padding(top = 4.px), text = Res.String.USEFUL_LINKS_LINK_NAME1)
        Box(modifier = Modifier.height(24.px))
        InCardText(modifier = Modifier, text = Res.String.USEFUL_LINKS_SUBTITLE2)
        LinkText(modifier = Modifier.padding(top = 4.px), text = Res.String.USEFUL_LINKS_LINK_NAME2)
        Box(modifier = Modifier.height(24.px))
        InCardText(modifier = Modifier, text = Res.String.USEFUL_LINKS_SUBTITLE3)
        LinkText(modifier = Modifier.padding(top = 4.px), text = Res.String.USEFUL_LINKS_LINK_NAME3)
        Box(modifier = Modifier.height(24.px))
        InCardText(modifier = Modifier, text = Res.String.USEFUL_LINKS_SUBTITLE4)
        Row(modifier = Modifier.padding (top = 4.px).fillMaxWidth()) {
            LinkText(text = Res.String.USEFUL_LINKS_LINK_NAME41)
            InCardText(text = Res.String.USEFUL_LINKS_LINK_DIVIDER, modifier = Modifier.padding(leftRight = 12.px))
            LinkText(text = Res.String.USEFUL_LINKS_LINK_NAME42)
        }
        Box(modifier = Modifier.height(46.px))
        Image(
            modifier = Modifier.fillMaxSize()
                .padding(leftRight = 40.px)
                .objectFit(ObjectFit.Cover),
            src = Res.Image.GOOGLE_PLAY_BADGE
        )
    }

}